import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Col, Row, Spin } from 'antd';
import ProjectCard from '../UiComponents/ProfilePage/ProjectCard';
import { Helmet } from 'react-helmet';

const AllProjects = () => {
  const [data, setdata] = useState();
  const [spin, setspin] = useState(false);
  useEffect(() => {
    setspin(true);
    axios
      .get('https://ginnovation-server.herokuapp.com/api/project')
      .then((resp) => {
        setspin(false);
        setdata(resp.data);
      })
      .catch((err) => {
        setspin(false);
        console.log(err);
      });
  }, []);
  return (
    <div className='all-blogs'>
      <Helmet>
        <title>Projects - Ginnovation Club</title>
        <meta
          name='description'
          content='Ginnovation club has well understood the importance of practical learning and continuously working with the students to make more future oriented projects.'
        />
      </Helmet>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      <div className='page-top-secondVarient project-top'>
        <header>
          <h2>Student Projects</h2>
          <p>Creative implementations of learning</p>
        </header>
      </div>
      <div className='container blog-container'>
        {data && (
          <Row gutter={8}>
            {data
              .filter((value) => value.status === 'approved')
              .map((value, index) => {
                return (
                  <Col md={8} key={index}>
                    <ProjectCard value={value} />
                  </Col>
                );
              })}
          </Row>
        )}
      </div>
    </div>
  );
};

export default AllProjects;
